import React from 'react';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);

import PageStatusConstants from '../../constants/PageStatusConstants';
import PageTextArea from './PageTextArea';
import UserProfile from '../../support/UserProfile';

export default class NewSubmission extends ParseComponent {
  constructor(props) {
    super(props);

    var story = new Parse.Object('Story');
    story.id = props.storyId;

    this.state = {
      storyId: props.storyId,
      story: story,
      refPageId: props.refPageId,
      creator: props.creator,
      userInput: null
    };

    this.handleChange = this.handleChange.bind(this);
  }

  componentDidUpdate() {
    if (this.state.userInput === null && this.data.drafts.length) {
      this.setState({userInput: this.data.drafts[0].content});
    }
  }

  observe(props) {
    var story = new Parse.Object('Story');
    story.id = props.storyId;

    return {
      user: ParseReact.currentUser,
      drafts: new Parse.Query('Page')
            .equalTo('story', story)
            .equalTo('statusRaw', PageStatusConstants.DRAFTED)
            .equalTo('creator', Parse.User.current())
            .ascending('createdAt')
            .include('creator')
    };
  }

  handleCancel(e) {
    e.preventDefault();

    this.setState({userInput: ''});

    this._deleteFromParse();
  }

  handleSaveDraft(e) {
    let content = this._gatherData(e);

    if (!content) return;

    if (this.data.drafts.length) {
      this._updateToParse(content);
    } else {
      this._saveNewToParse(content, PageStatusConstants.DRAFTED);
    }
  }

  handleSubmit(e) {
    this._deleteFromParse();
    this._saveNewToParse(this._gatherData(e), PageStatusConstants.PENDING);
  }

  handleChange(e) {
    this.setState({userInput: e.target.value});
  }

  _gatherData(e) {
    e.preventDefault();
    let content = this.state.userInput.trim();
    if (!content) {
      return;
    }

    return content;
  }

  _deleteFromParse() {
    if (this.data.drafts.length) {
      ParseReact.Mutation.Destroy(this.data.drafts[0]).dispatch();
    }
  }

  _updateToParse(content) {
    ParseReact.Mutation.Set(
      this.data.drafts[0], {content: content}
    ).dispatch();
  }

  _saveNewToParse(content, pageStatus) {
    ParseReact.Mutation.Create('Page', {
      content: content,
      statusRaw: pageStatus,
      refPageId: this.state.refPageId,
      story: this.state.story,
      likeCount: 0,
      creator: Parse.User.current()
    }).dispatch().then(() => {
      if (pageStatus !== PageStatusConstants.DRAFTED) {
        this.setState({userInput: ''});
      }
    });
  }

  render() {
    const user = this.data.user;

    return (
      <div className='new-submission small-24 large-24 vertical grid-block'>
        <PageTextArea
          onChange={this.handleChange}
          value={this.state.userInput}
          avatarUrl={UserProfile.getAvatar(user)}/>
        <div className='button-actions grid-block'>
          <div className='small-4 small-offset-1 medium alert hollow button' onClick={this.handleCancel.bind(this)} ref='cancelBtn'>Cancel</div>
          <div className='small-5 small-offset-7 medium hollow button' onClick={this.handleSaveDraft.bind(this)} ref='saveDraftBtn'>Save Draft</div>
          <div className='small-4 small-offset-1 medium success hollow button' onClick={this.handleSubmit.bind(this)} ref='submitBtn'>Submit</div>
        </div>
      </div>
    );
  }
}



